
.Masthead{position:relative;min-height:21.875rem;scroll-behavior:smooth;overflow:hidden;z-index:-1
}
@media screen and (min-width: 768px){
.Masthead{min-height:100vh
}
}
.Masthead__image{width:100%;overflow:hidden;height:120%
}
.Masthead__image>img{height:100%;max-width:none;width:100%;object-fit:cover;object-position:bottom;font-family:"object-fit: cover; object-position: bottom"
}
.Masthead__image.is-parallax{left:0;position:absolute;will-change:transform;right:0;top:0
}
.Masthead__image.is-parallax>img{height:100%;max-width:none;width:100%;object-fit:cover;object-position:bottom;font-family:"object-fit: cover; object-position: bottom"
}
.Masthead__image.is-fixed{position:fixed
}
.Masthead__image.is-fixed>img{height:100vh;max-width:none
}



